                <p class="text">单产显著提高。2010年平均每公顷粮食产量突破5000公斤。2018年达到5621公斤，比1996年的4483公斤增加了1138公斤，增长25%以上。2017年稻谷、小麦、玉米的每公顷产量分别为6916.9公斤、5481.2公斤、6110.3公斤，较1996年分别增长11.3%、46.8%、17.4%，比世界平均水平分别高50.1%、55.2%、6.2%。</p>
                
