var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"chapter1-container"},[_c('div',{staticClass:"chapter1-title"},[_vm._v("成 就")]),_c('div',{staticClass:"border"}),_c('div',{staticClass:"forward",on:{"click":_vm.toChapter5}},[_c('font-awesome-icon',{attrs:{"icon":['fas', 'forward'],"size":"3x"}})],1),_c('div',{staticClass:"backward",on:{"click":_vm.toChapter3}},[_c('font-awesome-icon',{attrs:{"icon":['fas', 'backward'],"size":"3x"}})],1),_c('div',{staticClass:"chapter1-box"},[_c('div',{staticClass:"subtitle"},[_vm._v("中国粮食安全成就")]),_c('p',{staticClass:"text"},[_vm._v("中国人口占世界的近1/5，粮食产量约占世界的1/4。中国依靠自身力量端牢自己的饭碗，实现了由“吃不饱”到“吃得饱”，并且“吃得好”的历史性转变。这既是中国人民自己发展取得的伟大成就，也是为世界粮食安全作出的重大贡献。")]),_c('p',{staticClass:"text"},[_vm._v("人均占有量稳定在世界平均水平以上。目前，中国人均粮食占有量达到470公斤左右，比1996年的414公斤增长了14%，比1949年新中国成立时的209公斤增长了126%，高于世界平均水平。")]),_c('p',{staticClass:"text"},[_vm._v("单产显著提高。2010年平均每公顷粮食产量突破5000公斤。2018年达到5621公斤，比1996年的4483公斤增加了1138公斤，增长25%以上。2017年稻谷、小麦、玉米的每公顷产量分别为6916.9公斤、5481.2公斤、6110.3公斤，较1996年分别增长11.3%、46.8%、17.4%，比世界平均水平分别高50.1%、55.2%、6.2%。")]),_c('div',{staticClass:"inner-image"},[_c('img',{attrs:{"src":_vm.img1}})]),_c('div',{staticClass:"inner-border"}),_c('div',{staticClass:"subtitle"},[_vm._v("谷物供应基本自给")]),_c('p',{staticClass:"text"},[_vm._v("实现谷物基本自给。2018年，谷物产量6.1亿吨，占粮食总产量的90%以上，比1996年的4.5亿吨增加1.6亿吨。目前，我国谷物自给率超过95%，为保障国家粮食安全、促进经济社会发展和国家长治久安奠定了坚实的物质基础。")]),_c('p',{staticClass:"text"},[_vm._v("确保口粮绝对安全。近几年，稻谷和小麦产需有余，完全能够自给，进出口主要是品种调剂，将中国人的饭碗牢牢端在自己手上。2001年至2018年年均进口的粮食总量中，大豆占比为75.4%，稻谷和小麦两大口粮品种合计占比不足6%。")]),_c('div',{staticClass:"inner-image"},[_c('img',{attrs:{"src":_vm.img2}})]),_c('div',{staticClass:"inner-border"}),_c('div',{staticClass:"subtitle"},[_vm._v("粮食储备能力显著增强")]),_c('p',{staticClass:"text"},[_vm._v("仓储现代化水平明显提高。2018年全国共有标准粮食仓房仓容6.7亿吨，简易仓容2.4亿吨，有效仓容总量比1996年增长31.9%。食用油罐总罐容2800万吨，比1996年增长7倍。规划建设了一批现代化新粮仓，维修改造了一批老粮库，仓容规模进一步增加，设施功能不断完善，安全储粮能力持续增强，总体达到了世界较先进水平。")]),_c('p',{staticClass:"text"}),_c('div',{stati